* {
  font-family: sans-serif;
  font-weight: lighter; }

html, body {
  height: 100%; }

body {
  background-color: #ccc; }

/* !!! Bootstrap !!!! */
.navbar-light .navbar-toggler {
  margin: 5px; }

.btn {
  margin: 5px; }

.row {
  margin: 10px;
  border-radius: 5px;
  padding: 10px 0px;
  background-color: #ddd; }

.navbar {
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.5);
  padding: 0px;
  background-color: #59535F; }

.navButtons .title {
  margin: 0px 10px;
  color: #FD8945;
  text-decoration: none;
  font-weight: bold; }

.navButtons .link {
  margin: 0px;
  margin-top: -1px;
  text-decoration: none;
  padding: 10px;
  color: #FD8945;
  border-top: 1px solid rgba(0, 0, 150, 0.1); }

.navButtons .full {
  padding: 0px 10px;
  margin-top: -1px;
  color: #F85649;
  border-top: 1px solid rgba(0, 0, 150, 0.1); }

.btn-outline-primary {
  color: #F85649;
  border-color: #F85649; }

.navButtons .current {
  line-height: 45px;
  height: 50px;
  margin: 0px;
  margin-top: -1px;
  padding: 0px 10px;
  background-color: rgba(0, 0, 0, 0.05);
  border-bottom: 3px solid rgba(0, 0, 150, 0.4); }

.footer {
  position: fixed;
  bottom: 0px;
  width: 100%;
}
.footer .container {
  padding: 0px;
  margin: auto;
}
.footer .row {
  background-color: rgba(0,0,0,0);
}
.footer .col-md-1 {
  margin: 0px;
  padding: 0px;
  min-width: 110px;
  border-radius: 3px;
  color: #444;
  margin: auto;
}
.footer .col-md-1:hover {
  background-color: rgba(128,0,0,0.075);
  color: #444;
}
/* color: rgba(128,0,0,0.7); */

.InfoSpan .spinner-grow, .spinner-border {
  color: brown;
  width: 90px;
  height: 90px;
  margin-top: 40px; }

.InfoSpan h1, .InfoSpan h3 {
  text-align: center;
  color: #59535F; }

.InfoSpan h1 {
  color: #FD8945; }

.InfoSpan .row {
  margin-bottom: 10px;
  padding: 10px;
  background-color: rgba(255, 255, 255, 0.2); }

.InfoSpan .col3 {
  height: 240px;
  padding-top: 20px; }

.InfoSpan .col3:not(:first-child) {
  border-left: 2px solid white; }

@media (max-width: 768px) {
  .InfoSpan .col3:not(:first-child) {
    border: none;
    border-top: 2px solid white; } }

.InfoSpan .value {
  padding-top: 50px; }

.home .subpage-content {
  padding: 10px;
  border-radius: 10px;
  min-height: 300px;
  background-color: #ccc;
}
.home .row {
  padding: 10px;
}
.home .subpage {
  padding: 10px;
  margin-top: 50px;
  margin-bottom: 50px;
}
.home .col-md-3 {
  padding-left: 0px;
}
.home .jumbotron {
  background-color: aliceblue;
  color: rgba(128,0,0,0.7);
  text-align: center;
  margin: 0px;
  padding: 10px;
  margin-top: 80px;
}
.home .link {
  width: 100%;
}
.home .current {
  background-color: rgba(0,0,0,0);
  color: rgb(40, 106, 209);
}
.home .imageContainer {
  text-align: center;
}
.home .roverSVG {
  width: 300px;
  margin: auto;
}

.note {
  position: relative;
  left: 0px;
  right: 0px;
  font-size: 20px;
  background-color: #666;
  margin: 8px;
  border-radius: 2px;
  padding: 5px;
  color: #eee;
  height: 40px;
  line-height: 30px;
}
.note.active {
  border: 4px solid brown;
  opacity: 0.35;
}
.note .container-btn {
  position: absolute;
  top: 0px;
  right: 5px;
}
.note .btn {
  color: brown;
  font-size: 25px;
  background-color: #888;
  height: 30px;
  line-height: 33px;
  padding: 5px;
  padding-top: 0px;
  border-radius: 2px;
  border: none;
}
.note .btn:hover {
  background-color: #aaa;
  cursor: pointer;
}
.note .remove-btn {
  float: right;
}
.note .update-btn {
  position: absolute;
  right: 40px;
  color: #bbb;
}

.form * {
  font-size: 25px;
  margin: 3px;
  padding: 3px 8px 3px 8px;
}
.form {
  margin: 10px;
}
.form input {
  width: 500px;
}
.form .cancel-btn {
  line-height: 32px;
}

.notes .notes-container {
  max-width: 700px;
}
.notes .heading {
  text-align: center;
  color: #444;
}

